/*TOP*/
#topDiv { z-index: 10; width: 100%; background: #fff;top:0;position: fixed; color:#fff !important;height:500px;min-width:320px;}
#topDiv .scrollDown{cursor: pointer; background: #ff617f no-repeat center ; background-size: auto 50%; width:50px;height:50px;display: inline-block;position: absolute; z-index: 5;bottom:-25px;border-radius: 50%;left:0;right:0;margin:auto;box-shadow: 0px 7px 15px -5px #aaa;transition:opacity 0.4s 1.5s, transform  0.4s 1.5s, background-color 0.5s; }
#topDiv .scrollDown img{opacity: 1;transition:opacity 0.5s;display:block;position: absolute;top:50%;left:50%;margin-left:-1px;transform:translate(-50%, -50%) translateZ(0);will-change:transform;height:25px;}
#topDiv .scrollDown img.hover{opacity: 0;transition:opacity 0.5s;}
#topDiv .scrollDown:hover{ background-color: #fff; transition: background-color 0.5s;}
#topDiv .scrollDown:hover img{opacity:0;transition:opacity 0.5s;}
#topDiv .scrollDown:hover img.hover{opacity: 1;transition:opacity 0.5s;}
#topDiv .banner{position:absolute; left:0;top:0;right:0;bottom:0; z-index: 1;}
#topDiv .banner *{position:absolute; left:0;top:0;right:0;bottom:0;}
#topDiv .banner .bg{background: #0b0c11  no-repeat  center top fixed; z-index: 1;}
#topDiv .banner .bpart1{background:no-repeat center top fixed; background-size: 1200px auto; z-index: 2; }
#topDiv .banner .bpart2{background: no-repeat center top fixed; background-size: 1200px auto; z-index: 3; }
#topDiv .banner .bpart3{background: no-repeat center top fixed; background-size: 1200px auto; z-index: 4; }
#topDiv .banner .bpart4{background:no-repeat center top -150px fixed; background-size:1200px auto; z-index: 5; }
#topDiv .banner .bpart5{background:no-repeat center top -150px fixed; background-size:1200px auto; z-index: 6; }
#topDiv .banner .bpart6{background:no-repeat  left top 0px; background-size: 1200px auto; z-index: 7; }
#topDiv .profile{position:absolute;z-index:3;transform:translate(-50%, -50%) translateZ(0);will-change: transform;top:50%;margin:auto;left:50%;width:auto !important;}
#topDiv .profile h1{display:inline-block; margin:0 0 0px;text-transform:uppercase;font-size:3rem;letter-spacing:4.5px;color:#fff;text-shadow: 5px 5px 5px #000;}
#topDiv .profile h1{ margin-top:0; }
#topDiv .profile .bio{ position: relative;}
#topDiv .bio-inner{ transform: translateZ(0);will-change:transform;}
 
#topDiv .profile.parallaxInit span{transition:none;}
#topDiv .profile .role{margin:0;text-transform:uppercase;color:#fff; letter-spacing: 3px; white-space: nowrap;line-height: 150%;display:flex;justify-content:center;}
#topDiv .profile .sub{text-align: center;left:0;right:0;margin:auto; position: absolute;top:0;bottom:0; letter-spacing: 4px;display: block; font-size: 0.85rem; transform: translateZ(0);will-change:transform; }
#topDiv .profile .desc{letter-spacing: 4px;white-space: nowrap;display: block; font-size: 0.85rem;margin-top:5px; display:flex;justify-content:center;}
#topDiv .profile .desc .sep{margin:0 5px;}
#topDiv .logo{ position: fixed;  z-index: 4; left:50px;}
#topDiv .logo img{transform: translateZ(0);will-change:transform;}
#topDiv .logo a{border:0px none;}

#topDiv .socialWrap{position: relative;display: inline-block;margin-top:5px;transform: translateZ(0);will-change:transform;}
#topDiv .socials ul{list-style: none;margin:0;padding:0;}
#topDiv .socials ul li{display: inline-block;margin:5px 5px 0;}
#topDiv .socials ul li a{display: block; text-decoration:none; border:0px none;border-radius: 50%; }
#topDiv .socials a img{height:32px; filter:invert(1); transition:filter 0.25s 0.25s;display: block;} 
#topDiv .socials a:hover img{  filter:invert(50%) sepia(10%) saturate(50) hue-rotate(-65deg) brightness(90%); transition:filter 0.25s 0.25s;} 
#topDiv .profileAfter {position:absolute;right:0px;top:9px;z-index:3;padding-right:50px;line-height: 125%;vertical-align:top;}
#topDiv .profileAfter .nameAfter{position:relative;color:#fff; font-family: "Work Sans", sans-serif; font-weight: 500; text-transform:uppercase; letter-spacing: 5px;font-size: 1.5rem;vertical-align: middle;display:inline-block;}
#topDiv .profileAfter .socialWrap{position:relative;vertical-align: middle;margin:0;}

#topDiv.compact{height:auto;position: fixed;text-align: left; padding: 11px 50px 7px; border-bottom: 4px solid rgb(255, 97, 127);}
#topDiv.compact .logo{position: relative;left:auto;display: inline-block;}
#topDiv.compact .logo img{height: 40px;transform: translateZ(0);will-change:transform;}

.topBarCTA{cursor:pointer;color:#fff;font-family:"Work Sans",sans-serif;text-transform:uppercase;letter-spacing:3px;font-size:1.5rem;padding:15px;display:inline-block;vertical-align:middle;border:0px none;position:absolute;z-index:3;left:50%;transform:translate(-50%,-50%) translateZ(0);will-change: transform;top:50%;}

/*IOS*/
@supports (-webkit-touch-callout: none) {
    
    #topDiv .banner .bpart1, #topDiv .banner .bpart2,
    #topDiv .banner .bpart3, #topDiv .banner .bpart4,
    #topDiv .banner .bpart5{background-attachment:scroll; }
}


@media screen and (max-width: 1440px) {

}


@media screen and (max-width: 1280px) {
    #topDiv.compact{padding: 11px 25px 7px; }
    #topDiv .profileAfter{padding-right:25px;}
    #topDiv .profileAfter .nameAfter{letter-spacing:2px;}
    #topDiv .logo{left:25px}

    .topBarCTA{letter-spacing:0px;}

}

@media screen and (max-width: 1024px) {
}


@media screen and (max-width: 960px) {
    #topDiv .profile h1{font-size:2rem;}
    #topDiv .profile .role{font-size:0.8rem;}
    #topDiv .profile .desc{font-size: 0.75rem;}
    #topDiv .profile .sub{letter-spacing:1.5px;display:block;font-size:0.75rem;}
    #topDiv .profile .socials a img{height:24px}
    #topDiv .scrollDown{width:40px;height:40px;bottom:-20px;}
    #topDiv .scrollDown img{margin:0px;height:20px;}
    #topDiv .profileAfter .nameAfter{font-size: 1.3rem;letter-spacing:0px;margin-right:5px;}
    #topDiv .socials a img{height: 25px;}
    #topDiv.compact .logo img{height:31px;}
    #topDiv.compact{padding: 11px 25px 6px; }
    .topBarCTA{font-size:1.3rem;}
    #topDiv .socials ul li{margin:5px 3px 0;}




    #topDiv .banner .bpart1, #topDiv .banner .bpart6{ background-size: 875px auto; }
    #topDiv .banner .bpart2, #topDiv .banner .bpart3, 
    #topDiv .banner .bpart4, #topDiv .banner .bpart5{background-size: 800px auto;}
}



@media screen and (max-width: 769px) {
    #topDiv .profile h1{font-size:2.5rem;}
    #topDiv .profile .role{font-size:0.975rem;}
    #topDiv .profile .desc{font-size: 0.875rem;}
    #topDiv .profile .sub{font-size:0.925rem;}

    #topDiv .profileAfter .nameAfter{display:none;}
    .topBarCTA{font-size:1.7rem;}

    #topDiv .profile .bio{}
    #topDiv .profile .bio .bio-inner{transform:translateX(-50%) translateZ(0);will-change: transform;left:50%;position: relative;}
 
}



@media screen and (max-width: 480px) {
    #topDiv .profileAfter{padding-right:15px;}
    #topDiv.compact{padding:11px 15px 6px;}
    #topDiv .logo{left:15px;}
    .topBarCTA{left:0;right:0;top:auto;bottom:-19px;transform:none;background:#ff617f;padding:5px 0 5px;text-align:center;font-size:0.65rem;letter-spacing:3px;}
    .topBarCTA .mobile-hide,
    .topBarCTA .tablet-hide{display:inline;}

    #topDiv .banner .bpart1, #topDiv .banner .bpart6{ background-size: 480px auto; }
    #topDiv .banner .bpart2, #topDiv .banner .bpart3, 
    #topDiv .banner .bpart4, #topDiv .banner .bpart5{background-size: 480px auto;}
}












